export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "task_title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Tasks Note"])},
        "car_insurance": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Car insurance"])}
      },
      "es": {
        "task_title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Anotador de tareas"])},
        "car_insurance": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Cotizador de Seguros"])}
      }
    }
  })
}
